import { HashRouter, Switch, Route } from 'react-router-dom';
import Main from "./components/Main";

function App() {
  return (
    <HashRouter>
      <Switch>
        <Route exact path="/" component={Main}/>
        <Route path="/wildlife" component={Main}/>
        <Route path="/fossils" component={Main}/>
      </Switch>
    </HashRouter>
  );
}

export default App;